@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,3000,4000,5000,6000,7001,600&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    width: 100%;
    height: 100%;
    font-size: 62.5%;
    @media screen and (max-width: 76.7rem) {
        font-size: 58%; }
    @media screen and (max-width: 48rem) {
        font-size: 62%; } }

body {
    font-family: 'Rubik';
    color: #ffffff;
    font-size: 1.8rem;
    font-weight: 500;
    background-color: #313840;
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%; }

main {
    width: 100%; }

.container {
    max-width: 118rem;
    width: 100%;
    margin: 0 auto; }

a {
    color: #fff;
    text-decoration: none; }

ul {
    list-style-type: none; }

.img {
    &__search-icon {
        min-width: 2.4rem; }

    &__lk-icon {
        min-width: 5rem; } }

.header {
    min-height: 10rem;
    display: flex;
    align-items: center;
    &__wrapper {
        font-weight: 500;
        font-size: 2.4rem;
        justify-content: space-between;
        margin: 0 auto;
        width: 100%;
        max-width: 118rem;
        display: flex;
        align-items: center; }
    &__wrapper-left {
        max-width: 39.3rem;
        width: 100%;
        display: flex;
        align-items: center;
        gap: 2rem;
        justify-content: space-between; }

    &__navigation {
        align-items: center;
        display: flex;
        width: 100%;
        max-width: 53.7rem; }
    &__navigation_list {
        justify-content: space-between;
        display: flex;
        width: 100%;
        align-items: center;
        gap: .2rem;
        max-width: 53.7rem;

        @media screen and (max-width: 76.7rem) {
            display: none; }
        @media screen and (min-width: 76.7rem) {
            display: flex; } }
    &__navigation_list-link {
        white-space: nowrap;
        padding: .2rem;
        display: flex;
        align-items: center; } }


.logo-link {
    max-width: 24rem;
    min-width: 5rem;
    width: 100%;
    @media  screen and ( max-width: 48rem) {
        display: none; }
    @media  screen and ( max-width: 60rem) {
        display: block; } }




.city-block {
    position: relative;
    align-items: center;
    margin: 0;
    padding: 0;
    box-shadow: border-box; }

.dropdown {

    &__city {
        display: flex;
        align-items: center;
        gap: 2.9rem; }

    &__city-selected {
        margin-bottom: 2.2rem;
        color: #9CA69E;
        align-items: center;
        font-size: 1.6rem;
        display: block; }

    &__city-title {
        font-weight: 500;
        font-size: 1.6rem;
        display: block; }
    &__city-wrapper {
        background: #36505D;
        font-size: 1.6rem;
        top: 0;
        left: 0;
        position: absolute;
        transform: translate(0, 8%);
        z-index: 999;
        font-weight: 400;
        min-width: 37rem;
        max-width: 37rem;
        display: none;
        width: 100%;
        padding: 2rem 1.4rem 1.7rem 2rem;
        @media screen and (max-width: 48rem) {
            max-width: 15rem;
            min-width: 15rem; } }
    &__city-list_wrapper {
        gap: 2.6rem;
        display: flex;
        @media screen and (max-width: 48rem) {
            flex-direction: column; } }
    &__city-list {
        display: flex;
        flex-direction: column;
        gap: .3rem; } }

.modal-block {
    display: flex;
    align-items: center;
    gap: 1.9rem; }

.form-wrapper {
    position: relative;
    color: #111; }

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4); }

.active {
    display: block; }
.modal-content {
    color: #111;
    background-color: #fefefe;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%; }


.close {
    float: right;
    font-size: 28px;
    font-weight: bold; }

#register-link {
    color: #111 !important; }
.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer; }


.btn-log {
    margin: 0 auto;
    width: 100%;
    max-width: 10rem;
    height: 3.5rem;
    border-radius: 2.5rem; }

.form-title {
    margin: 5rem auto 0 auto;
    font-size: 4.2rem;
    font-weight: 700;

    @media screen and (max-width: 76.8rem) {
      margin-bottom: 2rem; } }

.form-text {
    text-align: center;
    margin: 2rem auto 0 auto; }

.btn-log {
    width: 100%;
    max-width: 10rem;
    height: 3.5rem;
    border-radius: 2.5rem; }

